* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-weight: unset;
	font-size: inherit;
	color: inherit;
}

html {
	font-size: 62.5%;
}

body {
	margin-top: 80px;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	font-size: 1.8rem;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #cdcdcd28;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
}

button {
	cursor: pointer;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 40px;
	width: 40px;
	border: 0;
	background-color: transparent;
}

img {
	width: 100%;
	height: auto;
}

h1,
h2 {
	text-align: center;
}

h1 {
	margin-top: 2rem;
	font-size: 2.8rem;
}

form {
	text-align: center;
	font-size: 1.4rem;
}

input {
	width: 20%;
	margin-left: auto;
	padding: 0.25rem;
}

.red-text {
	color: red;
}

.orange-text {
	color: orange;
}

.green-text {
	color: green;
}
